  width: 100%;
  height: auto;
}

.page-container {
  width: 1160px;
  height: inherit;
  margin-left: auto;
  margin-right: auto;
}

.header_logged-out {
  display: grid;
  grid-template-columns: 391px 440px 329px;
  width: 100%;
  height: 70px;
  background-color: #FFFFFF;
}

.header_logged-in {
  display: none;
}

.header {
  width: 100%;
  height: 70px;
  background-color: #ffffff;
}

.navbar {
  position: relative;
  width: 100%;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html, body {
  min-height: 100%;
  height: 100%;
  width: 100%;
}

h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 29.26px;
}

h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 19px;
  font-weight: 700;
  line-height: 23.16px;
}

h3 {
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  font-weight: 700;
  line-height: 14.63px;
}

a {
  font-family: "Montserrat", sans-serif;
  color: rgba(31, 32, 65, 0.3137254902);
  font-family: "Montserrat";
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-decoration: none;
}

body p {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

li {
  list-style-type: none;
  text-decoration: none;
  margin: 0px;
  padding: 0px;
}

ul {
  list-style-type: none;
  text-decoration: none;
  margin: 0px;
  padding: 0px;
}

button {
  border: 0px;
  background-color: transparent;
}

.list {
  display: flex;
  justify-content: space-between;
}

.list__item {
  margin-top: 26px;
  margin-bottom: 26px;
  flex-shrink: 0;
  /* margin-left: 5px;
   margin-right: 5px;*/
}

