  content: "expand_more";
  color: rgba(31, 32, 65, 0.3137254902);
  text-align: center;
  font-family: "Material Icons";
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 1px; /* 20.4px */
  position: relative;
  top: 7px;
}

